




























































import HeadTtop from '../../components/head/head.vue'
export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '提交订单',
			backshow: true,
			payIndex: 0,
			list: [],
			userinfo: ''
		};
	},
	onLoad(e) {
		this.userinfo = uni.getStorageSync('userinfo')
		this.list = e
	},
	methods: {
		payLi(index) {
			this.payIndex = index
		},
		register() {
			var that = this;
			let params = {
				order_id: this.list.order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			if (that.userinfo.is_vip == 2) {
				if (this.payIndex == 0) { // 微信支付
					this.$http.HttpRequst.request(true, 'order/wxPay', params, 'POST', res => {
						uni.requestPayment({
							provider: 'wxpay',
							orderInfo: res.data, //订单数据
							success: function(res) {
								uni.showToast({
									title: '支付成功',
									icon: 'success',
									duration: 800,
									success() {
										setTimeout(res => {
											uni.switchTab({
												url: '/pages/info/home/home'
											})
										}, 800)
									}
								});
							},
							fail: function(err) {
								uni.showToast({
									title: '支付取消',
									icon: 'success',
									duration: 800
								});
							}
						});
					});
				}
				if (this.payIndex == 1) { // 支付宝支付
					this.$http.HttpRequst.request(true, 'alipay/pay', params, 'POST', res => {
						uni.requestPayment({
							provider: 'alipay',
							orderInfo: res, //订单数据
							success: function(res) {
								uni.showToast({
									title: '支付成功',
									icon: 'success',
									duration: 800,
									success() {
										setTimeout(res => {
											uni.switchTab({
												url: '/pages/info/home/home'
											})
										}, 800)
									}
								});
							},
							fail: function(err) {
								uni.showToast({
									title: '支付取消',
									icon: 'success',
									duration: 800
								});
							}
						});
					});
				}
			}else{ //余额支付
				if( uni.getStorageSync('userinfo').balance < this.allprice ){
					uni.showToast({
						title: '余额不足，请充值',
						icon: 'success',
						duration: 600,
						success() {
							setTimeout( res => {
								uni.redirectTo({
									url: '/pages/info/interests/interests'
								})
							},600)
						}
					});
				}else{
					this.$http.HttpRequst.request(false, 'order/balancePay', params, 'POST', res => {
						if( res.code == 200 ){
							uni.showToast({
								title: '成功',
								icon: 'success',
								duration: 600,
								success() {
									setTimeout( res => {
										uni.switchTab({
											url: '/pages/info/home/home'
										})
									},600)
								}
							});
						}
					});
				}
			}
		}
	}
}
